        <v-col cols="12" v-if="isSearching5_2">
          <v-list dense v-if="!isCardDetails_5">
            <v-list-item-group v-model="selectedItem" color="primary">
              <v-list-item v-for="(item, i) in infoListDirections" :key="i">
                <v-list-item-icon>
                  <v-icon>mdi-gesture-double-tap</v-icon>
                </v-list-item-icon>
                <v-list-item-icon :style="'margin-right: 15px;'">
                  {{ item.directions_kilometraje + " Km" }}
                </v-list-item-icon>
                <v-list-item-content>
                  <v-divider class="mx-4"></v-divider
                  ><v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-list-item-title
                        v-bind="attrs"
                        v-on="on"
                        v-text="item.directions_name"
                        @click="_isCardDetails_5(item)"
                      ></v-list-item-title>
                    </template>
                    <span>{{ item.directions_name }}</span>
                  </v-tooltip>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>

            <v-card-actions style="display: grid">
              <v-btn
                color="deep-purple lighten-2"
                text
                @click="isSearching5_()"
              >
                <v-icon>mdi-chevron-double-left</v-icon> Regresar
              </v-btn>
            </v-card-actions>
          </v-list>

          <div v-if="isCardDetails_5">
            <v-card-title style="font-weight: bold; color: #003f6b">{{
              detalle_.directions_name
            }}</v-card-title>
            <v-card-text>
              <div class="my-4 text-subtitle-2">
                {{ detalle_.directions }}
              </div>
            </v-card-text>
            <v-divider class="mx-4"></v-divider>

            <v-list dense>
              <v-list-item-group color="primary">
                <v-list-item>
                  <v-list-item-icon>
                    <v-icon>mdi-phone-in-talk</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>
                      {{
                        detalle_.directions_phone
                          ? detalle_.directions_phone
                          : "No registrado"
                      }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon>
                    <v-icon>mdi-av-timer</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      >{{
                        detalle_.directions_scheduler
                          ? detalle_.directions_scheduler
                          : "No registrado"
                      }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>

            <v-divider class="mx-4"></v-divider>

            <v-card-title>¿Qué deseas hacer?</v-card-title>

            <v-card-text>
              <v-chip-group>
                <v-chip>
                  <v-btn
                    text
                    :href="
                      'https://www.google.com/maps/dir/' +
                      detalle_.directions_from +
                      '/' +
                      detalle_.directions_to +
                      '/am=t'
                    "
                    target="_blank"
                  >
                    <v-icon>mdi-map</v-icon> LLÉVAME CON GOOGLE MAPS
                  </v-btn>
                </v-chip>
              </v-chip-group>
            </v-card-text>

            <v-card-actions>
              <v-btn
                color="deep-purple lighten-2"
                text
                @click="isCardDetails_Salir()"
              >
                <v-icon>mdi-chevron-double-left</v-icon> Regresar
              </v-btn>
            </v-card-actions>
          </div>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  props: ["infoListDirections", "infosDistrito_", "myProp"],
  watch: {
    myProp: function () {
      if (this.myProp.type == "notary") {
        this._isCardDetails_0(this.myProp);
      } else if (this.myProp.type == "press") {
        this._isCardDetails_1(this.myProp);
      } else if (this.myProp.type == "court") {
        this._isCardDetails_2(this.myProp);
      } else if (this.myProp.type == "police") {
        this._isCardDetails_3(this.myProp);
      } else if (this.myProp.type == "municipality") {
        this._isCardDetails_4(this.myProp);
      }
    },
  },

  data: () => ({
    tab: null,
    items: [
      { tab: "notary", content: "Notaria" },
      { tab: "press", content: "RRPP" },
      { tab: "court", content: "Poder Judicial" },
      { tab: "police", content: "Comisaria" },
      { tab: "municipality", content: "Municipalidad" },
    ],

    selectedItem: 1,
    itemsx: [
      { text: "Real-Time", icon: "mdi-clock" },
      { text: "Audience", icon: "mdi-account" },
      { text: "Conversions", icon: "mdi-flag" },
    ],

    isLoading: false,
    //PANELES DE TIPOS DE SERVICIOS
    isSearching0: true,
    isSearching1: false,
    isSearching2: false,
    isSearching3: false,
    isSearching4: false,
    isSearching5: false,
    isSearching5_1: false,
    isSearching5_2: false,
    //CARD DE DETALLE E IR AL MAPA// POBLADO DE LAS TARJETAS
    isCardDetails_0: false,
    isCardDetails_1: false,
    isCardDetails_2: false,
    isCardDetails_3: false,
    isCardDetails_4: false,
    isCardDetails_5: false,
    isCardDetails_5_1: false,
    isCardDetails_5_2: false,

    form: {
      type: "",
      department_id: "",
      province_id: "",
      district_id: "",
      site_id: "",
    },
    //DETALLE PARA EL CARD
    detalle_: {
      directions_from: "",
      directions_to: "",
      directions: "",
      directions_phone: "",
      directions_scheduler: "",
      directions_name: "",
    },

    options: {
      types: [
        { value: "notary", text: "Notaria" },
        { value: "press", text: "RRPP" },
        { value: "court", text: "Poder Judicial" },
        { value: "police", text: "Comisaria" },
        { value: "municipality", text: "Municipalidad" },
      ],
      departments: [],
      provinces: [],
      districts: [],
      sites: [],
    },
    results: [],
  }),
  computed: {
    searchEnabled() {
      return true;
      //return this.form.site_id > 0
    },
  },
  mounted() {
    this.getDepartments();
  },
  methods: {
    getDepartments() {
      let url = "ubigeo/departments";
      axios
        .get(url)
        .then((response) => {
          this.options.departments = response.data;
        })
        .catch()
        .then(() => {});
    },
    getProvinces(departmentId) {
      let url = "ubigeo/provinces?department_id=" + departmentId;
      axios
        .get(url)
        .then((response) => {
          this.options.provinces = response.data;
        })
        .catch()
        .then(() => {});
    },
    getDistricts(provinceId) {
      let url = "ubigeo/districts?province_id=" + provinceId;
      axios
        .get(url)
        .then((response) => {
          this.options.districts = response.data;
        })
        .catch()
        .then(() => {});
    },
    getSites(districtId) {
      let url = "sites?district_id=" + districtId + "&type=" + this.form.type;
      axios
        .get(url)
        .then((response) => {
          this.options.sites = response.data;
        })
        .catch()
        .then(() => {});
    },

    isSearching0_(data_) {
      //CAMBIO DE TAB DESPLIEGUE : 1
      this.isSearching0 = !this.isSearching0;
      this.isSearching1 = false;
      this.isSearching2 = false;
      this.isSearching3 = false;
      this.isSearching4 = false;
      this.isSearching5 = false;
      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },

    isSearching1_(data_) {
      //CAMBIO DE TAB DESPLIEGUE
      this.isSearching1 = !this.isSearching1;
      this.isSearching0 = false;
      this.isSearching2 = false;
      this.isSearching3 = false;
      this.isSearching4 = false;
      this.isSearching5 = false;
      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },
    isSearching2_(data_) {
      //CAMBIO DE TAB DESPLIEGUE
      this.isSearching2 = !this.isSearching2;
      this.isSearching1 = false;
      this.isSearching0 = false;
      this.isSearching3 = false;
      this.isSearching4 = false;
      this.isSearching5 = false;
      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },

    isSearching3_(data_) {
      //CAMBIO DE TAB DESPLIEGUE
      this.isSearching3 = !this.isSearching3;
      this.isSearching1 = false;
      this.isSearching2 = false;
      this.isSearching0 = false;
      this.isSearching4 = false;
      this.isSearching5 = false;
      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },

    isSearching4_(data_) {
      //CAMBIO DE TAB DESPLIEGUE
      this.isSearching4 = !this.isSearching4;
      this.isSearching1 = false;
      this.isSearching2 = false;
      this.isSearching3 = false;
      this.isSearching0 = false;
      this.isSearching5 = false;
      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },
    isSearching5_() {
      //CAMBIO DE TAB DESPLIEGUE
      this.isSearching5 = true;
      this.isSearching1 = false;
      this.isSearching2 = false;
      this.isSearching3 = false;
      this.isSearching0 = false;
      this.isSearching4 = false;
      this.isSearching5_1 = true;
      this.isSearching5_2 = false;
    },

    onSearch() {
      this.isSearching5 = true;
      this.isSearching5_1 = false;
      this.isSearching5_2 = true;
      this.isSearching1 = false;
      this.isSearching2 = false;
      this.isSearching3 = false;
      this.isSearching0 = false;
      this.isSearching4 = false;

      this.$emit("onSearch", this.form);
    },

    //POBLADO PARA LAS TARJETAS
    _isCardDetails_0(data_) {
      this.isCardDetails_0 = data_.type ? true : !this.isCardDetails_0;
      this._isCardDetails_Poblar(data_);
    },

    _isCardDetails_1(data_) {
      this.isCardDetails_1 = data_.type ? true : !this.isCardDetails_1;
      this._isCardDetails_Poblar(data_);
    },
    _isCardDetails_2(data_) {
      this.isCardDetails_2 = data_.type ? true : !this.isCardDetails_2;
      this._isCardDetails_Poblar(data_);
    },
    _isCardDetails_3(data_) {
      this.isCardDetails_3 = data_.type ? true : !this.isCardDetails_3;
      this._isCardDetails_Poblar(data_);
    },
    _isCardDetails_4(data_) {
      this.isCardDetails_4 = data_.type ? true : !this.isCardDetails_4;
      this._isCardDetails_Poblar(data_);
    },
    _isCardDetails_5(data_) {
      this.isCardDetails_5 = data_.type ? true : !this.isCardDetails_5;
      this._isCardDetails_Poblar(data_);
    },

    //POBLADO PARA LAS TARJETAS
    _isCardDetails_Poblar(data_) {
      this.detalle_.directions_from = data_.directions_from;
      this.detalle_.directions_to = data_.directions_to;
      this.detalle_.directions = data_.directions;
      this.detalle_.directions_phone = data_.directions_phone;
      this.detalle_.directions_scheduler = data_.directions_scheduler;
      this.detalle_.directions_name = data_.directions_name;
    },

    isCardDetails_Salir() {
      this.isCardDetails_0 = false;
      this.isCardDetails_1 = false;
      this.isCardDetails_2 = false;
      this.isCardDetails_3 = false;
      this.isCardDetails_4 = false;
      this.isCardDetails_5 = false;
    },
  },
};
</script>

<style lang="scss">
#map {
  height: calc(100vh - 56px);
}
</style>

<style lang="scss" scoped>
.search-box {
  position: absolute;
  background-color: white;
  border-radius: 0.5rem;
  left: 0.5rem;
  top: 0.5rem;
  width: calc(100% - 1rem);
  max-width: 300px;
  margin: 0 !important;
}
</style>